<template lang="pug">
    v-card
        v-toolbar(flat)
            v-toolbar-title 累计产量曲线
            v-spacer
            v-btn(color="success" @click="update_line_data") 更新曲线
        v-divider
        v-row
            v-col(cols="12" lg="2")
            v-col(cols="12" lg="8")
                v-combobox(
                        v-model="wellname"
                        :items="wellnames"
                        label="选择井号"
                    )
            v-col(cols="12" lg="2")
        v-divider
        div(ref="monthProdCusumElement" class="monthProdCusumClass")
            VegaChartView(
                :key="chartId"
                :chartSource="monthProdLineData.monthProdLineCusumData"
                :id="chartId"
                :chartId="chartIdStr"
                :chartNum="chartNum"
            )
        
            
                
</template>

<script>

import { get } from 'vuex-pathify'
import lineData from "./lineData.js"
import * as api from "@/apis/wswebapi"

export default {
  name: "MonthProdLineCusum",
  
  data: () => ({
        wellname:"ALL",
        monthProdLineData: lineData,
        chartNum:0,
        chartIdStr:"#MonthProdLineCusum",
        chartId:"MonthProdLineCusum",
  }),
  computed:{
      screenWidth:get("app/screenWidth"),
      wellnames:get("peproject/oilwellnames"),
      prod_line_data:get("peproject/prod_line_data"),
      prod_line_cusum_data:get("peproject/prod_line_cusum_data"),
      blockname:get("peproject/blockname"),
      
  },
  methods:{
    //   更新曲线
    update_line_data(){
        api.GetProdLineCusumData({blockname:this.blockname,collection:"oilreportmonth",wellname:this.wellname}).then(res=>{
            if (res.code==200){
                this.monthProdLineData.monthProdLineCusumData.datasets.monthProdLineCusumData=res.data;
                this.chart();
            }else{
                console.log("返回错误",res.code)
                this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true});
            }
        })
    },
      setChartId(){
          let cid = Math.random().toString(36).slice(-8);
          this.chartId=cid;
          this.chartIdStr="#"+cid;
          console.log("MonthProdLineCusum ",this.chartId,this.chartIdStr);
      },
      chart(){
          console.log("宽度:",this.$refs.monthProdCusumElement.clientWidth);
          this.monthProdLineData.monthProdLineCusumData.width=this.$refs.monthProdCusumElement.clientWidth-150;
          this.chartNum=this.chartNum+1;
      }
  },
    watch:{
        screenWidth(){
            console.log("窗口变化");
            this.setChartId();
            setTimeout(this.chart,2000);
        }
    },
    components: {
      VegaChartView: () => import('@/views/charts/vegacharts/VegaChartView'),
    },

};
</script>

<style>
.monthProdCusumClass{
    width:100%
}
</style>